import { VoiceLogger } from "./utils/logger";

export class VoiceWidget {
  private root: HTMLElement;
  private logger = new VoiceLogger();

  constructor(root: HTMLElement) {
    this.root = root;
  }

  mount(): void {
    this.root.innerHTML = `
      <div class="voice-widget">
        <div data-test="status" class="voice-widget__status">Idle</div>
        <button data-action="start">Start Voice Session</button>
      </div>
    `;

    this.root.querySelector("[data-action='start']")?.addEventListener("click", () => {
      this.logger.log("Start pressed");
      this.root.querySelector("[data-test='status']")!.textContent = "Startingâ€¦";
    });
  }
}

export function createVoiceWidget(root: HTMLElement): VoiceWidget {
  const widget = new VoiceWidget(root);
  widget.mount();
  return widget;
}
